input:disabled ~ .field-radio__name-text {
  cursor: not-allowed;
}

.field-radio__name-text:before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid currentColor;
  border-radius: 50%;
}
