  .aside-form__title {
    font-size: 25px;
  }
}
.aside-form__text {
  margin-bottom: 20px;
}
.aside-form form {
  margin-top: 40px;
}
@media (max-width: 767px) {
  .aside-form form {
    margin-top: 20px;
  }
}
.aside-form__submit.btn {
  margin-top: 30px;
}

.close {
  position: relative;
  opacity: 1;
}
.close.aside-form__close {
  margin-left: auto;
  width: 23px;
  height: 23px;
}
.close.aside-form__close::before, .close.aside-form__close::after {
  height: 23px;
  width: 2px;
}

.close:hover {
  opacity: 0.4;
}

.close:before, .close:after {
  position: absolute;
  top: 0;
  left: 50%;
  content: " ";
  width: 14px;
  height: 1px;
  background-color: #000;
  transform: translateX(-50%);
}

.close:before {
  transform: translateX(-50%) rotate(45deg);
}

.close:after {
  transform: translateX(-50%) rotate(-45deg);
}

